import axios from "axios";

export const fetchRepos = (search, page) => {
  return axios
    .get(
      `https://api.github.com/search/repositories?q=${search}&page=${page}&per_page=10`
    )
    .then(res => res.data);
};


export const searchWeather = (cityName) => {

  return axios
  .get(
    `http://jisutqybmf.market.alicloudapi.com/weather/query?city=${cityName}`,{
      headers:{
        "Authorization":"APPCODE e8bf4a8efe57469aaf3062cf6c4bb2ae"
      }
    })
  .then(res => res)
  .catch(err => err)
}

export const ipLocation = (ip) => {
  return axios
  .get(
    `http://iploc.market.alicloudapi.com/v3/ip?ip=${ip}`,{
      headers:{
        "Authorization":"APPCODE e8bf4a8efe57469aaf3062cf6c4bb2ae"
      }
    }
  )
  .then(res => res)
  .catch(err => err)

}


export const getIpAddress = () => {

  return axios
  .get(
    "http://api.ip138.com/query/?datatype=jsonp&callback=find&token=5b38d9c6afba54e7396ba798ec17439f"
  )
  .then(res => res)
  .catch(err => err)

}



