import React, { Component } from "react";
import {Input,Form,Button,Card} from "antd"
import {observer,inject} from "mobx-react"
// import DevTools from 'mobx-react-devtools';
import "./weather.css"
const FormItem = Form.Item;

@inject("weatherStore")
@observer
class SearchView extends Component {

  handleSubmit = (e) => {
    const { searchAction } = this.props.weatherStore;
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        searchAction(values.cityName);
      }
    });
  }

  render(){
    const { getFieldDecorator } = this.props.form;
    const {searchState,loading } = this.props.weatherStore;
    return ( 
      <div className="search-container">
      <Form layout="inline" onSubmit={this.handleSubmit}>
        <FormItem 
        validateStatus={searchState} 
        help={searchState === "error"?"请输入正确的中国城市名":null}
        >
        {getFieldDecorator('cityName')(
          <Input  placeholder="请输入城市名" />
        )}
        </FormItem>
        <FormItem>
          <Button
            htmlType="submit"
            loading = {loading}
          >
            查询
          </Button>
        </FormItem>
      </Form>

      </div>
    )
  }
}
const WrappedSearch =  Form.create()(SearchView)

const TimePoint = props => {
  const {info} = props
  return (
    <div className="time-point">
      <div>{info.time}</div>
      <img src={`https://marvinx.oss-cn-shenzhen.aliyuncs.com/icon/${info.img}.png`}/>
      <div>{info.temp+"℃"}</div>
    </div>
  )
}

const RealTime = inject("weatherStore")(
  observer(
    (props) => {
      const {realtTimeWeather} = props.weatherStore;
      return realtTimeWeather ? (
        <Card hoverable className="real-time">
          <div className="date-container">
            <a>{realtTimeWeather.date + " " + realtTimeWeather.week}</a>
          </div>
          <div className="temp">
            <div>
              <a>{realtTimeWeather.temp+"℃"}</a> {realtTimeWeather.weather}
            </div>
            <img src={`https://marvinx.oss-cn-shenzhen.aliyuncs.com/icon/${realtTimeWeather.img}.png`}/>
          </div>
          <div className="info-tip-container">
            <div className="info-tip">{realtTimeWeather.windInfo}</div>
            <div className="info-tip">空气湿度 {realtTimeWeather.humidity}</div>
            <div className="info-tip">空气质量 {`「${realtTimeWeather.quality}」`} </div>
          </div>
          <div className="time-point-container">
            {
              realtTimeWeather.hourlyInfo.map((item)=>{
                return <TimePoint key={item.id} info={item}/>
              })
            }
          </div>
          <div className="reminder-container">
                {
                  realtTimeWeather.tips.map((item)=>{
                    return (
                      <p key={item.id}>
                        <a>
                          {item.iname + " "}
                        </a>
                        {item.ivalue} <br/>
                        {item.detail} <br/>
                      </p>
                    )
                  })
                }
          </div>
        </Card>
      ):null
    }
  )
) 

const Forecast =  (props) => {
  const { item } = props
    return (
      <Card hoverable className="forecast">
        <div>{`${item.date} ${item.week}`}</div>
        <div className="day-night">
          <div>
            <div>白天</div>
            <div>{item.day.temphigh+"℃"}</div>
            <div>
              <img src={`https://marvinx.oss-cn-shenzhen.aliyuncs.com/icon/${item.day.img}.png`}/>
            </div>
            <div>{item.day.weather}</div>
            <div>{item.day.windpower}</div>
          </div>
            -
          <div>
            <div>晚上</div>
            <div>{item.night.templow+"℃"}</div>
            <div>
              <img src={`https://marvinx.oss-cn-shenzhen.aliyuncs.com/icon/${item.night.img}.png`}/>
            </div>
            <div>{item.night.weather}</div>
            <div>{item.night.windpower}</div>
          </div>
        </div>
      </Card>
    )
  }
 


const LocationCity = inject("weatherStore")(observer((props) => {
      const { currentCity } = props.weatherStore;
      return (
        <div className="location">
          <img src="https://marvinx.oss-cn-shenzhen.aliyuncs.com/icon/location.png"/>
          <a>{currentCity}</a>
        </div>
      )
    }
  )
)

@inject("weatherStore")
@observer
export default class Weather extends Component {

  componentWillMount(){
    const {ipLocation} = this.props.weatherStore;
    ipLocation();

  }

  render() {
    const {forecastWeather} = this.props.weatherStore;
    return ( 
      <div className="container">
        <div>
          <LocationCity />
          <WrappedSearch />
          <RealTime/>
          <div className="forecast-container ">
          {
            forecastWeather.map((item)=>{
              return (<Forecast key={item.id} item={item}/>)
            })
          }
          </div>
        </div>
      </div>
    );
  }
}


