"user strict";
import {observable,action,autorun,runInAction,configure} from "mobx"
import moment from "moment"
import shortid from "shortid"
import {searchWeather,ipLocation,getIpAddress} from "../api"

configure({enforceActions:true});

export default class searchWeatherStore {

    @observable currentCity = "定位中...";

    @observable locating = true;

    @observable searchState =  "";  //  validating  success  error 

    @observable loading = false;

    @observable realtTimeWeather = null;
    @observable forecastWeather = [];

    @action
    searchAction =async (cityName) => {
        if (!cityName) return;
        this.loading = true;
        const res = await searchWeather(cityName);
        runInAction(()=>{
            this.loading = false;
            if (res.status != 200) {
                this.searchState = "error";
                return;
            }
            this.searchState = "success";
            const data = res.data;
            const date = moment(data.result.date).format("YYYY年MM月DD日");
            this.realtTimeWeather = {
                date:date,
                week:data.result.week,
                temp:data.result.temp,
                weather:data.result.weather,
                windInfo:data.result.winddirect + " " +data.result.windpower,
                humidity:data.result.humidity,
                quality:data.result.aqi.quality,
                img:data.result.img,
                hourlyInfo:data.result.hourly.map((item)=>{
                    return {
                        ...item,
                        id:shortid.generate()
                    }
                }),
                tips:data.result.index.map((item)=>{
                    return {
                        ...item,
                        id:shortid.generate()
                    }
                })
            }
            this.forecastWeather = data.result.daily.filter((item,idx)=>{
                    return idx != 0;
            }).map((item,idx)=>{
                return {
                    ...item,
                    id:shortid.generate(),
                    date:moment(item.date).format("MM月DD日")
                }
            });
            this.currentCity = data.result.city;
        })
    }

    @action
    ipLocation = async () => {
        const ipRes = await getIpAddress();
        if (ipRes.data) {
            const ip = ipRes.data.ip;
            const locationRes =  await ipLocation(ip);
            const city = locationRes.data.city;
            if (city.length > 0) {
                this.searchAction(city);
            }else {
                runInAction(()=>{
                    this.currentCity = "不知道在哪儿";
                })
            }            
        }else {
            runInAction(()=>{
                this.currentCity = "不知道在哪儿";
            })
            
        }

    }

}


